* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Saira";
  outline: none;
}

#root,
body,
html {
  height: 100%;
}
button {
  cursor: pointer;
}

.container {
  height: 100%;
  background: url("./assets/cover.jpg");
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}

.header {
  background-color: rgba(0, 0, 0, 0.2);
  color: #fff;
  height: 100%;
  background-image: url("./assets/header.svg");
  background-repeat: no-repeat;
  background-size: 100%;
}

.content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  width: 100%;
  max-width: 1200px;
  margin: 0px auto;
}

.content .left {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 100%;
}
.left img {
  max-width: 100%;
}
.content nav ul {
  list-style-type: none;
}
.content nav ul li {
  display: inline;
  margin: 2px 5px;
}

.content nav li button {
  border: none;
  color: #000;
  font-size: 15px;
  padding: 0px 10px;
  border-bottom: 2px solid transparent;
  background: transparent;
}

.content nav li button:hover {
  border-bottom: 2px solid #000;
}

footer {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 56px;
  background: #13263a;
  color: #fff;
  flex-direction: row;
}

@media only screen and (max-width: 1150px) {
  .header {
    background-size: 140%;
  }
}

@media only screen and (max-width: 900px) {
  .content nav ul {
    display: none;
  }
  .left img {
    max-width: 25vw;
  }
  .content .left {
    flex: 1;
    max-width: 100%;
  }
}
@media only screen and (max-width: 650px) {
  .header {
    background-size: 230%;
  }
  .container {
    background-attachment: scroll;
  }
  footer h4 {
    font-size: 13px;
  }
}
