.service-section {
	height: 100vh;
}
.service-section img {
	height: 100vh;
	max-width: 100%;
}
.service-container {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}

.service-content {
	display: flex;
	flex-direction: column;
	justify-content: center;
	max-width: 600px;
	margin: 0px auto;
	text-align: center;
}

.service-content h1 {
	font-size: 50px;
	padding-bottom: 15px;
	color: #13263a;
}

.service-content p {
	font-size: 20px;
	text-align: left;
	color: #13263a;
}



@media only screen and (max-width:1200px){
	.service-section img {
		height: 100vh;
		max-width: 35.7%;
	}

}
@media only screen and (max-width:900px){
	.service-section img {
		height: 100vh;
		display: none;
	}
	.service-container{
		height:100%;
		align-items: center;
		justify-content: center;
		margin:20px;
	}
	.service-content h1 {
		font-size: 30px;
	}
	.service-content p{
		font-size: 16px;
	}
}